.Hedear2{
    width: 100%;
    height: 150px;
    
    margin: auto;
    position: relative;
    align-items: center;
    display: flex;
    
    background-color: rgb(12, 12, 12);
}
.Hedear {
    position: relative;
    margin: 2em 0;
    padding: 0.5em 1em;
    /* border: solid 3px #349dff; */
    border-radius: 8px;
    width: 1300px;
    height: 150px;
    margin: auto;
    background-color: black;
    color: #349dff;
    text-align: center;
}

.Image{
    width: 250px;
    /* border: solid 3px #349dff; */
    position: absolute;
    margin-left: 3%;
    margin-top: -1%;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    
}
.TitleFont{
    text-align: center;
    user-select: none;
    
    position: relative;
    display        : inline-block;
    color          : black;            
    font-size      : 60px;               
    letter-spacing : 2px;             
    text-shadow    : 
        2px  2px 1px #238df8,
        -2px  2px 1px #003366,
        2px -2px 1px #003366,
        -2px -2px 1px #003366,
        2px  0px 1px #003366,
        0px  2px 1px #003366,
        -2px  0px 1px #003366,
        0px -2px 1px #003366;        
}




