::-webkit-scrollbar {
    width: 5px;
}
 
::-webkit-scrollbar-track {
    background-color: #2f7dc6;
}
 
::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 6px rgb(255, 255, 255);
}



.Create{
    width: 1300px;
    height: 1000px;
    margin: auto;
    margin-top: 4%;
    
    background-color: rgba(28, 27, 27, 0);
}

.After,.Before{
    width: 49%;
    height: 80%;
    float: left;
    background-color: rgba(25, 25, 25, 0);
    margin: auto;
    
    position: relative;
    display: flex;
    align-items: center;
    z-index: 1;
}

.After{
    margin-left: 2%;
    
}

#ImgFrame,#ImgFrame2{
    width: 98%;
    height: 98%;
    margin-top: -62%;
    margin-left: -1%;
    
    position: absolute;
    z-index: 5;
    
    overflow: auto;

}

#ImgFrame{
    cursor: pointer;
}

#ImgSize,#ImgeSize2{
    width: 100%;
    height: auto;
}


.Frame,.Frame2,.checkcolor{
    width: 100%;
    padding:62% 0.5em;
    border: dashed 3px #349dff;
    margin-left: 0.7%;
    /* background-color: rgb(75, 75, 75); */
    border-radius: 2%;
}

.Frame2{
    border: solid 3px #349dff;   
}

.loading{
    display: none;
    position: absolute;
    width: 300px;
    height: 100px;
    margin-left: 25%;
    border-radius: 10px 10px;
    text-align: center;
    color: #349dff;
    font-size: 40px;

}


.checkcolor{
    display: none;
    position: absolute;
    background-color: gainsboro;
    background-image: -webkit-linear-gradient(45deg,  #808080 25%, #808080 25%, transparent 25%, transparent 75%, #808080 75%, #808080 75%),
                        -webkit-linear-gradient(-135deg, #808080 25%, #808080 25%, transparent 25%, transparent 75%, #808080 75%, #808080 75%);
    background-image: linear-gradient(45deg,  #808080 25%, #808080 25%, transparent 25%, transparent 75%, #808080 75%, #808080 75%),
                        linear-gradient(-135deg, #808080 25%, #808080 25%, transparent 25%, transparent 75%, #808080 75%, #808080 75%);
    background-size: 30px 30px;
    -webkit-background-size: 30px 30px;
    background-position: 0 0, 15px 15px;
}


.download{
    cursor: pointer;
    width: 90px;
    height: 90px;
    border: solid #349dff;
    margin-left:63%;
    margin-top: 0.5%;
    border-radius: 5%;
    position: absolute;
    color: rgba(25, 25, 25, 1);
    font-size: 19px;
    display: flex;
    align-items: center;
    z-index: 99;
    user-select: none;

}

.UploadIcon{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-left: 260px;
    margin-top: -25px;
    position: absolute;
    border: dashed 3px #349dff;
    text-align: center;
    font-size: 60px;
    color: #349dff;
    cursor: pointer;

}

#filename{
    display: none;
}


.Colorpallet{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: aliceblue;
    margin: auto;
    border: solid 4px #349dff;
    cursor: pointer;
}

.Threshold{
    top: 60px;
    position: absolute;
    color: #349dff;
}

.threshold_text{
    width: 37px;
}

.ImageSize{
    top: 30px;
    position: absolute;
    color: #349dff;
}
.ImageSize_text{
    width: 37px;
}


.ControlBar{
    top: 3%;
    width: 1290px;
    height: auto;
    margin: auto;
    display: flex;
    position: relative;
    background-color: rgba(29, 29, 29, 0);
    border: solid 4px #349dff;
}

.RangeBox{
    position: absolute;
    width: 300px;
    height: 90px;
    left: 23%;
}


.Start{
    width: 90px;
    height: 90px;
    position: absolute;
    border-radius: 5%;
    margin-left: 55%;
    margin-top: 0.5%;
    border: solid 3px #349dff;
    cursor: pointer;
    color: #349dff;
    text-align: center;
    display: flex;
    align-items: center;
    font-size: 22px;
    /* background-color: #349dff; */
}

#BackGroundContorol{
    position: absolute;
    width: 360px;
    height: 90px;
    border: solid 3px #349dff;
