    background-image: linear-gradient(45deg,  #808080 25%, #808080 25%, transparent 25%, transparent 75%, #808080 75%, #808080 75%),
                        linear-gradient(-135deg, #808080 25%, #808080 25%, transparent 25%, transparent 75%, #808080 75%, #808080 75%);
    background-size: 30px 30px;
