.container {
    width: 100px;
    height: 100px;
    background: #f3f3f3;
    display: flex;
    padding: 30px;
    box-sizing: border-box;
    align-items: center;
  }
  
  .containerLeft {
    padding-right: 50px;
  }
  
  .bar {
    background: rgb(255, 255, 255);
    width: 100px;
    height: 40px;
    border: 1px solid #dedede;
    border-radius: 2px;
    display: flex;
    cursor: pointer;
  }
  .textColor{
      color: white;
  }
  .barLeft {
    flex: 1;
    padding: 10px;
  }
  
  .barRight {
    height: 100%;
    border-left: 1px solid #dedede;
    width: 40px;
  }
  
  .colorBox {
    background: #fff;
    border: 1px solid #dedede;
    width: 40px;
    height: 40px;
  }
  
  .pikker {
    position: absolute;
    top: 380px;
    left: 30px;
  }
  
  .pikkerBack {
    position: relative;
    width: 10%;
    height: 10%;
    top: 0;
    left: 0;
  }

  
  .color_position{
    margin: auto;
}